import React, { useState, useEffect } from "react";
import ProgressBar from "react-progressbar";
import "./QuizGame.css";

const QuizGame = ({ quizData }) => {
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [score, setScore] = useState(0);
  const [userAnswer, setUserAnswer] = useState("");
  const [showResult, setShowResult] = useState(false);
  const [remainingTime, setRemainingTime] = useState(30); //    (30 )
  const [showHint, setShowHint] = useState(false);

  const [shuffledQuizData, setShuffledQuizData] = useState([]);

  useEffect(() => {
    setShuffledQuizData(shuffleArray(quizData));
  }, [quizData]);

  useEffect(() => {
    if (showResult) {
      return; //       
    }

    if (remainingTime > 0) {
      // 1  
      const timer = setTimeout(() => {
        setRemainingTime((prevTime) => prevTime - 1);
      }, 1000);

      //     
      return () => clearTimeout(timer);
    } else {
      //       
      goToNextQuestion();
    }
  }, [remainingTime, showResult]);

  const currentQuestion = shuffledQuizData[currentQuestionIndex];

  const handleAnswer = (selectedAnswer) => {
    if (showResult) {
      goToNextQuestion();
    } else {
      setUserAnswer(selectedAnswer);

      const currentQuestion = shuffledQuizData[currentQuestionIndex];

      if (selectedAnswer === currentQuestion.correctAnswer) {
        setScore((prevScore) => prevScore + 1);
      }

      setShowResult(true);

      setTimeout(() => {
        goToNextQuestion();
      }, 2000);
    }
  };

  const renderScore = () => {
    return <div className="score">: {score}</div>;
  };

  const goToNextQuestion = () => {
    setUserAnswer("");
    setShowResult(false);
    setRemainingTime(30); //       (30 )
    setShowHint(false);

    if (currentQuestionIndex + 1 < shuffledQuizData.length) {
      setCurrentQuestionIndex((prevIndex) => prevIndex + 1);
    } else {
      //      
      alert(` !   ${score} .`);
      setCurrentQuestionIndex(0);
      setScore(0);
      setShuffledQuizData(shuffleArray(quizData));
    }
  };

  const renderQuestion = () => {
    if (!currentQuestion) {
      return null;
    }

    return (
      <div className="question-container">
        <h2 className="question-title">{currentQuestion.question}</h2>

        {/*     */}
        <div className="time-bar-container">
          <ProgressBar completed={(remainingTime / 30) * 100} color="#9b59b6" height="20px" />
        </div>

        <ul className="options-list">
          {currentQuestion.options.map((option, index) => (
            <li key={index}>
              <button
                className={`option-button ${
                  userAnswer === option ? (userAnswer === currentQuestion.correctAnswer ? "correct-answer" : "incorrect-answer") : ""
                }`}
                onClick={() => handleAnswer(option)}
              >
                {option}
              </button>
            </li>
          ))}
        </ul>

        {/*    */}
        {showHint && <p className="hint">{currentQuestion.hint}</p>}
        <button className="hint-button" onClick={() => setShowHint(true)} disabled={showHint}>
          Hint
        </button>
      </div>
    );
  };

  const shuffleArray = (array) => {
    const shuffledArray = [...array];
    let currentIndex = shuffledArray.length,
      randomIndex;

    while (currentIndex !== 0) {
      randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex--;

      [shuffledArray[currentIndex], shuffledArray[randomIndex]] = [shuffledArray[randomIndex], shuffledArray[currentIndex]];
    }

    return shuffledArray;
  };

  return (
    <div className="quiz-game-container">
      {renderScore()}
      {renderQuestion()}
      {showResult && (
        <p className={userAnswer === currentQuestion.correctAnswer ? "correct-answer" : "incorrect-answer"}>
          {userAnswer === currentQuestion.correctAnswer ? "!" : `. : ${currentQuestion.correctAnswer}`}
        </p>
      )}
    </div>
  );
};

export default QuizGame;
